const Firework = require("./firework");

class Demo {
  constructor(canvas, button) {
    this.canvas = canvas;
    this.context = canvas.getContext("2d");
    this.context.globalCompositeOperation = 'source-over';

    this.demoButton = button;

    this.playDemo = this.playDemo.bind(this);
  }

  listenToButton() {
    console.log("setting a listener on the demo button");
    this.demoButton.addEventListener("click", this.playDemo);
  }

  playDemo() {
    console.log("inside playDemo");

    this.context.clearRect(0, 0, document.documentElement.clientWidth, document.documentElement.clientHeight);


    let firework1 = new Firework(this.canvas, null, 103, 149);
    firework1.initiate();

    let firework2 = new Firework(this.canvas, [[255, 102, 1]], 721, 119);
    firework2.initiate();

    let firework3 = new Firework(this.canvas, null, 248, 395);
    firework3.initiate();

    let firework4 = new Firework(this.canvas, [[1, 255, 1]], 803, 293);
    firework4.initiate();

    let firework5 = new Firework(this.canvas, [[255, 102, 1]], 332, 467);
    firework5.initiate();

    let firework6 = new Firework(this.canvas, [[255, 102, 1]], 646, 556);
    firework6.initiate();

    let firework7 = new Firework(this.canvas, null, 932, 494);
    firework7.initiate();

    this.context.clearRect(0, 0, document.documentElement.clientWidth, document.documentElement.clientHeight);


  }


}

module.exports = Demo;



